@use "global" as *;

// 制作者情報
.author {
  margin-top: rem(80);
  padding-top: rem(40);
  padding-bottom: rem(40);
  @include mq(md) {
    margin-top: rem(100);
    padding-top: rem(48);
    padding-bottom: rem(48);
  }
}

.author__inner {
  text-align: center;
}

.author__text {
  font-size: rem(12);
  color: $black;
  @include mq(md) {
    font-size: rem(14);
  }
}

.author__link {
  display: inline-flex;
  align-items: center;
  gap: rem(6);
  color: inherit;
  text-decoration: underline;
  transition: opacity 0.3s;

  &:hover {
    opacity: 0.7;
  }
}

.author__logo {
  vertical-align: middle;
  height: rem(20);
  width: auto;
}
